          clearInterval(intervalId);
          this.startCounters();
        }
      }, 500);
    },
    methods: {
      startCounters() {
        this.numbersAndFacts.forEach(element => {
          setInterval(() => {
            if (element.current < element.number) {
              element.current = element.current + element.number / 100;
            }
          }, 1);
        });
